<template>
  <div >
    <div class="mhead" v-if="!hideHead">
      <ul>
        <li>
          <text>自助缴费机</text>
        </li>
      </ul>
    </div>
    <div class="loadview">
      <div class="auto">
        <div class="loading-text-style">
          <span class="text-style span-height-center">{{loading_text}}{{dots}}</span>
        </div>
        <div class="ErrorBtnLocation">
          <button class="InitErrorBtn btn btn-danger" v-if="errorShow" @click="start">重试</button>
        </div>
      </div>
    </div>
    <div class="foot" v-if="!hideHead" @click="home()" >
      <text >退出</text>
    </div>
    <div class="foot" v-if="hideHead" @click="back()">
      <text >返回</text>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    title: '初始化工作框',
    name: "Init",
    data() {
      return {
        loading_text: '正在对机器进行初始化操作,请稍等',
        dots: '',
        timeOut: undefined,
        timeIndex: 0,
        errorShow: false
      }
    },
    ready() {
      this.start()
    },
    props: {
      hideHead: false
    },
    methods: {
      //判断当前运行环境
      isAndroidDevice() {
        try {
          HostApp
          this.$AndroidPlugin.isAndroid = true
          Vue.ProxyUrl = this.$AndroidPlugin.getProxyURL()
        } catch (e) {
          console.log('PC测试环境')
          Vue.AndroidPlugin.isAndroid = false
          //如果是非安卓环境，config中配置的链接地址肯定是不能用的，所以改成空的即可
          Vue.ProxyUrl = ''
        }
      },
      back(){
        this.$back()
      },
      getSn() {
        this.Sn = this.$AndroidPlugin.getSN()
        Vue.PosUtil.Sn = this.Sn
      },
      home() {
        this.$AndroidPlugin.back_home()
      },
      /**
       * 给等待条添加一个点
       * */
      addDots() {
        this.timeIndex++
        this.dots += '.'
      },
      /**
       * 结束本组件内的计时器
       * */
      clearThisInterval() {
        window.clearInterval(this.timeOut)
        this.initDots()
      },
      /**
       * 清空等待条中的点
       * */
      initDots() {
        this.timeIndex = 0
        this.dots = ''
      },
      /**
       * 启动计时器
       * */
      async start() {
        await this.isAndroidDevice()
        await this.getSn()
        this.errorShow = false
        this.loading_text = '正在进行初始化操作,请稍等...'
        this.timeOut = window.setInterval(this.setTimeOutMethod, 1000)
        getConfig(this)
      },
      /**
       * 判断增加一个点还是清空点
       * */
      setTimeOutMethod() {
        this.timeIndex >= 3 ? this.initDots() : this.addDots()
      }
    }
  }
  /**
   * 获取后台配置文件来进行初始化工作
   * */
  let getConfig = self => {
    console.log('传的数据',self.$PosUtil.Sn)
    self.$resetpost(Vue.ProxyUrl +'/rs/logic/getConfigParam', {data: {device_num: self.$PosUtil.Sn}}, {resolveMsg: null, rejectMsg: null}).then(res => {
      self.loading_text = "初始化成功"
      self.clearThisInterval()
      // 设置从后台拿过来的参数值
      Object.assign(self.$PosUtil.posParam,res.data)
      // 验证通过
      self.$goto('main', {}, 'self')
    }).catch(res => {
      console.log('res.status:', res.status)
      if (res.status === 501) {
        self.loading_text = "该设备未注册，请注册后点击重试"
      } else if (res.status === 404) {
        self.loading_text = "设备网络故障或连接服务器异常"
      } else if (res.status === 500) {
        self.loading_text = "服务器内部错误"
      } else {
        self.loading_text = "未知错误"
      }
      self.errorShow = true
      self.clearThisInterval()
    })
  }
</script>

<style>
  .span-height-center {
    position: relative;
    top: 35%;
  }

  .text-style {
    font-size: 30px;
    color: black;
    font-weight: bolder;
  }

  .div-height10 {
    height: 10%
  }

  .div-height20 {
    height: 20%
  }

  .div-height30 {
    height: 30%
  }

  .div-height40 {
    height: 40%
  }

  .div-height50 {
    height: 50%
  }

  .div-height60 {
    height: 60%
  }

  .div-height70 {
    height: 70%
  }

  .div-height80 {
    height: 80%
  }

  .div-height90 {
    height: 90%
  }

  .div-height100 {
    height: 100%
  }

  .loading-text-style {
    margin: 0px 10% 0px 10%;
    height: 30%;
    border-radius: 10px;
    text-align: center
  }

  .InitErrorBtn {
    width: 150px;
    height: 60px;
    font-size: 20px;
  }

  .ErrorBtnLocation {
    position: relative;
    height: 0%;
  }
</style>



